body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #e6f2ff;
}

input[type='text'] {
  display: inline-block;
  margin-right: 10px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  margin-bottom: 10px;
  transition: all 0.3s ease-in-out;
}

input[type='text']:focus {
  outline: none;
  box-shadow: 0px 0px 5px rgba(0,119,204,0.5);
}

.employee {
 border-radius: 5px;
 box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
 padding: 20px;
 margin-top:10px;
background-color:white; 
transition:all .3s ease-in-out; 
border-left:5px solid #0077cc
}

.employee:hover {
box-shadow:0px 
0px 
10px rgba(0,0,0,.3); 
border-left-color:#005999; 
transform:scale(1.02)
}

.employee p {
margin:0
}

.employee p + p {
margin-top:10px
}

.employee span {
display:inline-block; 
width:120px; 
text-align:right; 
background-color:#f2f2f2; 
padding-right:10px; 
font-weight:bold; 
margin-right:10px; 
border-radius:5px
}

.skills,
.projects,
